/* People Page Styles */

/* Container */
.people-container {
  padding: 40px 20px;
  max-width: 900px; /* Smaller width to accommodate long bios */
  margin: 0 auto;
}

/* Title */
.people-title {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
}

/* Team Section Titles */
.team-section-title {
  font-size: 2rem;
  font-weight: bold;
  margin-top: 40px;
  text-align: left;
}

/* Team Grid */
.team-grid {
  display: flex;
  flex-direction: column; /* Stack cards vertically */
  gap: 20px; /* Space between cards */
}

/* Team Cards */
.team-card {
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  padding: 20px;
  display: flex;
  flex-direction: row; /* Cards layout: image on left, bio on right */
  align-items: center;
}

.team-card-image-container {
  width: 150px;
  height: 150px;
  overflow: hidden;
  margin-right: 20px; /* Space between image and text */
}

.team-card-image {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensure image fits within the container */
}

/* Card Body */
.team-card-body {
  flex: 1; /* Allow bio text to take up remaining space */
}

.team-card-name {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.team-card-role {
  font-size: 1rem;
  color: #666;
  margin-bottom: 10px;
}

.team-card-bio {
  font-size: 0.9rem;
  line-height: 1.5;
  color: #555;
}

.team-card-link {
  color: #007acc;
  text-decoration: none;
  font-size: 0.9rem;
}

.team-card-link:hover {
  text-decoration: underline;
}
