/* Hero Section Styles */
.hero {
  background: url("../assets/hero-bg.jpg") no-repeat center center/cover;
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #fff;
  padding: 0 20px;
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.hero-content {
  max-width: 1200px;
  margin: 0 auto;
  z-index: 2;
  padding: 30px 20px;
  background: rgba(
    0,
    0,
    0,
    0.3
  ); /* Slightly darker overlay for text readability */
  border-radius: 10px;
}

.hero-content h1 {
  font-size: 4rem; /* Larger font for title */
  margin-bottom: 10px;
  line-height: 1.2;
  font-weight: 600;
  color: #ffffff;
  text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.7); /* Stronger shadow for readability */
}

.lab-name {
  font-size: 2.5rem; /* Larger font size for lab name */
  margin: 0 auto;
  line-height: 1.5;
  color: #ffffff; /* CUHK Gold */
  font-weight: bold;
  white-space: nowrap; /* Prevent text wrapping */
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5); /* Add shadow for better contrast */
}

/* Responsive styling */
@media (max-width: 768px) {
  .hero-content h1 {
    font-size: 3rem; /* Responsive font size for smaller screens */
  }

  .lab-name {
    font-size: 2rem; /* Responsive font size for smaller screens */
  }
}
